// .editable { background:url('../images/icon_edit.png') center right no-repeat !important; }

.button { display:inline-block; padding:10px 20px; margin:0 0 10px 0; color:white; background-color:$majorGreen; font-size:15px; border:none; cursor:pointer; border-radius:3px; -webkit-appearance:none; line-height:1.4em;
    
    @media (max-width: 640px) { font-size:16px; }

    &:hover,
    &:focus { color:white; background-color:$majorGreen; }

    &.bookNow { width:100%; text-align:center; font-size:25px; padding:25px; text-transform:uppercase; font-weight:bold; margin:0; 
        
        @media (max-width: 640px) { font-size:16px; padding:20px; }
    }

    &.grey { background-color:#bebebe; }

    &.facebook,
    &.googlePlus,
    &.registerWithEmail { border-radius:5px; padding: 15px; font-weight:bold; color:#fff; width:100%; text-align:center; }

    &.facebook { background:#3b5997; }
    &.googlePlus { background: #de4b39; }


    &.white-border { background:none; border:solid 1px #fff; 
        &:hover,
        &:focus,
        &.active { background:#fff; color:$majorGreen}
    }

    &.additional { background-image:url('../images/mobile/04_list_a_space/icon_add.png'); background-position: center left 20px; background-repeat: no-repeat; background-size:30px; padding: 15px 35px 15px 60px !important; }
    &.remove { width:50px; height:50px; padding:0 !important; background:url('../images/mobile/04_list_a_space/icon_remove.png') center center no-repeat; }
}

.shorter { width:calc(100% - 50px)}
.shorterFriend { width:50px; height:58px; position:absolute; right:0; top:0; display:table;
    div{ display:table-cell; vertical-align:middle; text-align:right; }
}

.specialInput { width:100%; margin:0; padding:15px; border:solid 1px #cccccc; color:#444; border-radius:3px; 
    .dollar { display:block; float:left; width:50px; height:26px; font-size:18px; padding-top:1px; }
    input[type=text] { width:calc(100% - 50px - 95px - 30px); float:left; padding:0; border:none; height:26px !important; color:$majorGreen; }
    .per-month { width:95px; float:left; text-align:right; font-size:18px; padding-top:1px; }
}
