.memberSettings{ background:#fff9d9 url('../images/desktop/banner/list-a-space.jpg') center top no-repeat; background-size: 100%; background-repeat: no-repeat;
    
    @media (max-width: 840px) { background: url('../images/mobile/General/banner/bg_01.jpg') center top no-repeat; background-size: 100%; }

    .content { margin:0;
        
        h3 { color:#000; font-size:22px; }

        .wrapper-map { width:100%; padding-bottom:45%; position:relative; display:block; 
        
            iframe { position:absolute; top:0; left:0; width:100%; height:100%; width:100%; height:100%; border:0; z-index:101; }
        }
    }

    // ALERT
    .memberAlert{ 

    }

    // PAYMENT
    .memberPayment{ margin:0 0 0 -20px;

        > div {
            @media (max-width: 840px) { width:100%; }
        }

        > div > div { margin:0 0 20px 20px;
            
            .wrapper { position:relative; margin:0;

                h4 { position:relative; margin:0 0 20px 0;

                    &.special { padding: 0 0 0 35px;
                        span { display:block; position:absolute; top:50%; left:0; margin-top:-13px; width:24px; height:24px; color:#fff; background:#000; border-radius:20px; padding:0px 7px; font-size:16px; }
                    }
                }
            
                .wrap { margin:0 0 0 -20px; display:table; width:calc(100% + 20px);

                    @media (max-width: 640px ) { margin:0 0 0 -15px; width:calc(100% + 15px) }

                    > div { display:table-cell; vertical-align:middle; 
                        
                        @media (max-width: 480px ) { display:block; width:100%; }
                    }

                    > div > div { margin:0 0 20px 20px; 
                        
                        @media (max-width: 640px ) { margin:0 0 20px 15px; }

                        .button { margin:0 0 0 10px; 
                            &:first-child{ margin:0; }
                        }
                    }
                }

                .cardIcons { position:absolute; right:-10px; top:-4px; display:table;
                    span { display:table-cell; }                    
                }
            }
        }

        .border-box { background:#f3f3f3; padding:20px; 
            .button { margin:0; }
        }

        .spacer {
            @media (max-width: 480px ) { display:none; }
        }
    }
}
