.helpCenter {

  background: #fff9d9 url(../images/desktop/banner/help.jpg) center top no-repeat;

  //Tab
  .wrapper-tabs {

    .tab-content {

      .inner {

        background-color: #FFF;
        padding: 0;
      }
    }
  }

  //Accordion
  .accordion {

    .accordion__item {

      margin-bottom: 1.5rem;

      .accordion__title {

        background-color: #f3f3f3;
        padding: 1rem;

        > h3 {

          margin: 0;
        }

        &[aria-selected="true"] {

          background-color: #1dbeb9;

          > h3 {

            color: #FFF;
          }
        }
      }

      .accordion__body {

        padding: 1rem;
        background-color: #f3f3f3;
      }
    }
  }

  //Show more
  .wrapper-show-more{

    padding-bottom: 0;
  }
}