.memberWishList,
.memberBookings { background:#e0e0e0;

    .paddingFix { padding-top:0; }

    .content{ margin:0;

        .wrapper-tabs {

            ul.nav-tabs { display:table; width:calc(100% + 140px); margin: 0 0 45px -70px; 
                
                @media (max-width: 640px) { width:calc(100% + 40px); margin: 0 0 20px -20px; }

                >li { margin:0; float:none; display:table-cell; width:33.34%; vertical-align: middle; background:#bfbfbf; border-right:solid 1px #e1e1e1; 
                    
                    &:first-child { 
                        @media (max-width:1500px) { border-left:solid 1px #e1e1e1; }
                    }

                    &.active { background:$majorGreen;}

                    >a { display:block; color:white; font-size:24px; font-weight:normal; text-transform:uppercase; line-height:1.2em; padding:15px; text-align:center; border-radius:0; word-spacing: 9999999px;
                        @media (max-width: 980px) { font-size:20px; }
                        @media (max-width: 640px) { font-size:16px; }
                        @media (max-width: 480px) { font-size:14px; }
                    }
                }
            }
        }
    }
}

.memberWishList .content .wrapper-tabs ul.nav-tabs > li { width:33.34%; }
.memberBookings .content .wrapper-tabs ul.nav-tabs > li { width:50%; }
